<template>
    <div class="exam-main-center">
        <!--scrolling="no"-->
        <iframe-component :iframe-url="iframeUrl"
                          @close="close"
                          style="width: 100%;height: 100%;"
                          iframe-id="testIframe"></iframe-component>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    import iframeComponent from '@/common/iframeComponent.vue'
    let iframeUrl = ''
    export default {
        data: () => ({
            iframeUrl:''
        }),
        beforeRouteEnter(to,from,next){
            let store = window.vm.$store
            let router = window.vm.$router
            if(to.query.webUrl){
                iframeUrl = to.query.webUrl
                next()
            }else {
                router.push({
                    name:'sign_in_success'
                })
            }
        },
        methods:{
            close(){
                let vm = window.top.vm
                let from = this.$route.query.from
                if(from === 'readTestList'||from === 'courseStudyTypeList'){
                    let params = {
                        arrangement_id: this.$route.query.arrangement_id,
                        task_classify: this.$route.query.task_classify
                    }
                    vm.goView(from,params)
                }else {
                    vm.goView(from)
                }
            }
        },
        mounted(){
            if(iframeUrl){
                this.iframeUrl = iframeUrl
            }
        },
        computed: {

        },
        components: {
            iframeComponent
        },
        created() { 
        }
    }
</script>
