export default {
  data: function data() {
    return {
      maxMainH: 1000,
      maxMainW: 1366,
      mainW: 0,
      mainH: 0
    };
  },
  components: {},
  methods: {
    resizeWin: function resizeWin() {
      var _this = this;

      var root = document.documentElement || document.body;
      var h = root.clientHeight;
      var w = root.clientWidth; //编程页面全屏展示

      if (this.$route.name === 'bianchengTest') {
        _this.mainH = h;
        _this.mainW = w;
        return;
      } //13.3寸特殊适配


      if (w >= 1200 && h <= 700) {
        _this.mainW = 900;
        _this.mainH = h;
        return;
      }

      if (h > _this.maxMainH) {
        _this.mainH = _this.maxMainH;
      } else {
        _this.mainH = 0;
      }

      if (w > _this.maxMainW) {
        _this.mainW = _this.maxMainW;
      } else {
        _this.mainW = 0;
      } //                window.console.log(_this.mainW,_this.mainH)

    }
  },
  computed: {
    getBoxStyle: function getBoxStyle() {
      var res = {}; //                mainH?{height:mainH+'px'}

      if (this.mainH) {
        res.height = this.mainH + 'px';
      }

      if (this.mainW) {
        res.width = ['examEvaluation'].includes(this.$route.name) ? "100%" : this.mainW + 'px';
      }

      return res;
    }
  },
  destroyed: function destroyed() {
    window.removeEventListener('resize', this.resizeWin);
  },
  mounted: function mounted() {},
  created: function created() {
    //考试iframe宽度最大1366 高度最大1000
    this.resizeWin();
    window.addEventListener('resize', this.resizeWin);
  },
  watch: {}
};