<template>
    <div class="exam-main-center">
        <!--scrolling="no"-->
        <iframe-component :iframe-url="iframeUrl"
                          @close="close"
                          style="width: 100%;height: 100%;"
                          iframe-id="testIframe"></iframe-component>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    import iframeComponent from '@/common/iframeComponent.vue'
    let iframeUrl = ''
    export default {
        data: () => ({
            iframeUrl:''
        }),
        beforeRouteEnter(to,from,next){
            let store = window.vm.$store
            let router = window.vm.$router
            let actionMethod = 'course/getBookTaskWebViewInputDataActions'
            let params = {
                task_id:to.query.task_id,
                to:to
            }
            store.dispatch(actionMethod,params).then((res)=>{
                let {error_code,data} = res
                if(!error_code){
                    iframeUrl = data.complete_web_view_url
                    if(iframeUrl){
                        next()
                    }
                }else{
                    if(error_code === 20002 || error_code === 20003 || error_code === 20004){
                        router.push({
                            name:'sign_in_success'
                        })
                    }
                }
            })
        },
        methods:{
            close(){
                let vm = window.top.vm
                let from = this.$route.query.from
                if(from === 'courseReadTask'||from === 'courseStudyTypeList'){
                    let params = {
                        lesson_id: this.$route.query.lesson_id,
                        task_classify: this.$route.query.task_classify
                    }
                    vm.goView(from,params)
                }else {
                    vm.goView(from)
                }
                //vm.$router.go(-1)
            }
        },
        mounted(){
            if(iframeUrl){
                this.iframeUrl = iframeUrl
            }
        },
        computed: {

        },
        components: {
            iframeComponent
        },
        created() {
        }
    }
</script>
