export default {
  props: {
    iframeId: {
      type: String
    },
    iframeUrl: {
      type: String
    },
    isBiancheng: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      iframeLoading: true,
      iframe: null
    };
  },
  methods: {
    iframeLoadEvent: function iframeLoadEvent() {
      this.iframeLoading = false;
    },
    messageEvent: function messageEvent(data) {
      //window.console.log(data,arguments,'获取到iframe里得回调事件');
      //let iframeOrigin = ''+window.location.protocol+'//'+this.iframeUrl.replace(/^(http|https):\/\//g,'').split('/')[0];
      // console.log(data.origin,'data.origin')
      // console.log(iframeOrigin,'iframeOrigin')
      //判断是否json字符串
      if (typeof data.data === 'string') {
        try {
          var obj = JSON.parse(data.data);
          this.$emit(obj.type, obj.data);
        } catch (e) {
          var res = data.data;
          this.$emit(data.data, data);
        }
      }
    },
    removeIframeEvents: function removeIframeEvents() {
      this.iframe && this.iframe.removeEventListener('load', this.iframeLoadEvent);
      window.removeEventListener('message', this.messageEvent);
    }
  },
  destroyed: function destroyed() {
    this.removeIframeEvents();
  },
  mounted: function mounted() {
    this.iframe = document.getElementById(this.iframeId);
    this.iframe.addEventListener('load', this.iframeLoadEvent);
    window.addEventListener('message', this.messageEvent, false);
  },
  watch: {
    iframeUrl: {
      handler: function handler(nv) {},
      immediate: true
    }
  },
  computed: {},
  components: {},
  created: function created() {}
};