    //时间转换 大于60秒 转为X'XX秒
    transAudioTime: function transAudioTime(list) {
      list.forEach(function (subItem) {
        if (subItem.media_type == "2") {
          subItem.parameter_data.duration = parseInt(subItem.parameter_data.duration);

          if (subItem.parameter_data.duration % 60 === 0) {
            subItem.parameter_data.duration = "".concat(subItem.parameter_data.duration / 60, "'");
          } else if (subItem.parameter_data.duration > 60 && subItem.parameter_data.duration < 3600) {
            var num = parseInt(subItem.parameter_data.duration / 60);
            subItem.parameter_data.duration = "".concat(num, "'").concat(subItem.parameter_data.duration - num * 60, "\"");
          } else if (subItem.parameter_data.duration < 60) {
            subItem.parameter_data.duration = "".concat(subItem.parameter_data.duration, "\"");
          }
        }
      });
    }
