<template>
  <div  :class="isWxApp=='1'?'report-bg2':'report-bg'" :style="{
  'height':resultEnvironment === 'mobile'?'100%':isBigClient === true&&studyReport.assessment.assessment_content===null&&studyReport.has_task==='0'?currentClientHeight:'',
  'width':isBigClient===true?'80%':'100%',
  'margin':isBigClient===true?'0 auto':'',
  'min-height':resultEnvironment==='pc'?'100%':''}">
    <div class="report-header">
      <span class="left-content">
        <img  v-if="isWxApp!=='1'" @click="back" src="@/assets/img/back-btn.png" />
      </span>
      <span class="header-title" :title="studyReport.lesson_name">{{ studyReport.lesson_name }}</span>
      <!-- <span class="right-content">
        <img src="@/assets/img/ico-cup.png" />
        <span class="num">15</span>
      </span> -->
    </div>
    <div v-if="studyReport.is_all_complete==='1'" style="position: relative;">
      <div class="header-bg">
        <p>恭喜，本课任务已完成</p>
      </div>
    </div>
    <div class="report-content"  :style="resultEnvironment === 'mobile'?{'overflow': 'auto','overflow-y': 'scroll', '-webkit-overflow-scrolling': 'touch'}:{}">
      <div class="user-info" :style="studyReport.is_all_complete==='1'?{'margin-top':'0.337rem'}:{'margin-top':'0.251rem'}">
        <div class="user-img">
          <img :src="studyReport.student_info.head_image" />
        </div>
        <p>{{ studyReport.student_info.name }}同学</p>
        <div class="tip">本节课你的学习结果如下</div>
      </div>
      <div class="line"></div>
      <div class="report-teacher-eval">
        <p class="mod-tit">
          <span class="left-bor"></span>
          <span class="mod-txt">老师课评</span>
        </p>
        <div v-if="studyReport.assessment.assessment_content != null">
          <div class="teacher-eval-box">
            <div class="stu-info" style="width:auto;text-align: center">
              <img :src="studyReport.lesson_teacher_image" alt=""/>
              <p>{{studyReport.lesson_teacher_name}}</p>
            </div>
            <div class="teacher-eval">
              <p class="en">
                {{ studyReport.assessment.assessment_content.show_assessment }}
              </p>
              <button class="button-class" @click="translate">翻译</button>
            </div>
          </div>
          <div
            class="teacher-eval-box"
            style="margin-top:0.167rem"
            v-for="(item, index) in studyReport.assessment.assessment_content.assessment_result"
            :key="index"
          >
          <div style="display: flex;">
            <div class="stu-score-info-pink" :style="getResultStyle(index)">
              <p class="content">{{ item.result_name }}</p>
              <p class="score">{{ item.result }}</p>
            </div>
          </div>  
            <div class="teacher-score-eval">
              <div class="eval-content" :title="item.result_content">
                <p>{{ item.result_content }}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="no-practice-content" v-else>
          <p>别急, 老师课评马上就来~</p>
        </div>
        <div v-if="studyReport.has_task === '0'" style="margin-top:0.3rem"></div>
        <div v-if="studyReport.has_task !== '0'">
          <p class="mod-tit">
            <span class="left-bor"></span>
            <span class="mod-txt">学习反馈</span>
          </p>
          <div class="practice-feedback">
            <div class="practice-title">
              <h1>课前资源</h1>
              <img src="@/assets/img/ico-line.png" />
            </div>
            <div class="practice-content"
              v-for="(item, index) in studyReport.preview.task_record_list"
              :key="index">
              <div class="practice-top">
                <div class="left">
                  {{ item.task_group_name }}
                  <span
                    class="chapter-name"
                    v-if="item.task_group_type === '2'"
                    >{{ item.showObj.chapter_name }}</span
                  >
                </div>
                <div class="right">
                  <p v-if="isWxApp!='1'">
                    <!-- {{ item.task_group_type === "2" ? "最佳评价" : "练习评价" }} -->
                    评价
                  </p>
                  <star
                    v-model="item.showObj.star_num"
                    :class="isWxApp=='1'?'star-list-4':'star-list-3'"
                  ></star>
                </div>
              </div>
              <div class="practice-main">
                <span class="question" v-if="item.task_group_type === '1'">错题</span>
                <span class="answer" v-if="item.task_group_type === '1'">{{ item.showObj.error_num}}</span>
                <span class="question" v-if="item.task_group_type === '1'">正确率</span>
                <span class="answer" v-if="item.task_group_type === '1'">{{ item.showObj.accuracy }}<span class="unit">%</span></span>
                <span class="question" v-if="item.task_group_type === '3'">已订正</span>
                <span class="answer" v-if="item.task_group_type === '3'">{{item.showObj.revision_count}}</span>
                <!-- <span class="question" v-if="item.task_group_type === '2'"
                  >累计听读</span
                >
                <span class="answer" v-if="item.task_group_type === '2'"
                  >{{ item.showObj.time_total }}
                  <span class="unit">{{ item.showObj.time_name }}</span>
                </span> -->
                <span class="question" v-if="item.task_group_type === '2'">已打卡</span>
                <span class="answer" v-if="item.task_group_type === '2'">{{ item.showObj.day_num }}<span class="unit">天</span></span>
              </div>
              <div class="practice-footer" v-if="item.showObj.teacher_assessment">
                <p>老师评语：</p>
                <div class="remark">
                  {{ item.showObj.teacher_assessment }}
                </div>
              </div>
            </div>
            <div class="no-practice-content" v-if="studyReport.preview.has_task==='0'">
              <p>该节课没有课前资源哦~</p>
            </div>
            <div class="no-practice-content" v-if="studyReport.preview.has_task==='1'&&studyReport.preview.is_done==='0'">
              <p>你还没完成任何课前资源哦~~</p>
            </div>
          </div>
          <div  class="practice-feedback">
            <div class="practice-title">
              <h1>课后复习</h1>
              <img src="@/assets/img/ico-line.png" />
            </div>
            <div class="practice-content"  v-for="(item, index) in studyReport.review.task_record_list" :key="index">
              <div class="practice-top">
                <div class="left">{{ item.task_group_name }}
                  <span class="chapter-name" v-if="item.task_group_type === '2'">{{ item.showObj.chapter_name }}</span>
                </div>
                <div class="right">
                  <p v-if="isWxApp!='1'">
                    <!-- {{ item.task_group_type === "2" ? "最佳评价" : "练习评价" }} -->
                    评价
                  </p>
                  <star
                    v-model="item.showObj.star_num"
                    :class="isWxApp=='1'?'star-list-4':'star-list-3'"
                  ></star>
                </div>
              </div>
              <div class="practice-main">
                <span class="question" v-if="item.task_group_type === '1'">错题</span>
                <span class="answer" v-if="item.task_group_type === '1'">{{item.showObj.error_num}}</span>
                <span class="question" v-if="item.task_group_type === '1'">正确率</span>
                <span class="answer" v-if="item.task_group_type === '1'">{{ item.showObj.accuracy }}
                  <span class="unit">%</span>
                </span>
                <span class="question" v-if="item.task_group_type === '3'">已订正</span>
                <span class="answer" v-if="item.task_group_type === '3'">{{
                  item.showObj.revision_count
                }}</span>
                <!-- <span class="question" v-if="item.task_group_type === '2'"
                  >累计听读</span
                >
                <span class="answer" v-if="item.task_group_type === '2'"
                  >{{ item.showObj.time_total }}
                  <span class="unit">{{ item.showObj.time_name }}</span>
                </span> -->
                <span class="question" v-if="item.task_group_type === '2'">已打卡</span>
                <span class="answer" v-if="item.task_group_type === '2'">{{ item.showObj.day_num }}
                  <span class="unit">天</span>
                </span>
              </div>
              <div class="practice-footer" v-if="item.showObj.teacher_assessment">
                <p>老师评语：</p>
                <div class="remark">
                  {{ item.showObj.teacher_assessment }}
                </div>
              </div>
            </div>
            <div class="no-practice-content" v-if="studyReport.is_lesson_finish==='0'">
              <p>下课后才有课后复习哦~</p>
            </div>
            <div class="no-practice-content" v-if="studyReport.review.has_task==='0'&&studyReport.is_lesson_finish==='1'">
              <p>该节课没有课后复习哦~</p>
            </div>
             <div class="no-practice-content" v-if="studyReport.review.has_task==='1'&&studyReport.review.is_done==='0'&&studyReport.is_lesson_finish==='1'">
              <p>你还没完成任何课后复习哦~~</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="fixed-img">
      <img src="@/assets/img/ico-lulu.png" />
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import star from "@/common/star.vue";
export default {
  data: () => ({
    //isTest:'1',
    translateEn: true,
    studyReport: {
      assessment: {},
      preview: {
        task_record_list:[]
      },
      review: {
        task_record_list:[]
      },
      student_info: {},
    },
    //判断是否是wx小程序访问, 从url参数取到数据
    isWxApp:'0',
    // 运行环境 phone或pc
    resultEnvironment:'',
    isBigClient:false,
    currentClientHeight: '',
    screenWidth: document.documentElement.clientWidth || document.body.clientWidth,
    screenHeight: document.documentElement.clientHeight || document.body.clientHeight,
    bgHeaderLeft:''
  }),
  methods: {
    ...mapActions("course", ["getStudyReportActions"]),
    // 根据设计稿 计算出fontSize 自适应页面
    setRootFontSize() {
        let designWidth = 750
        let baseNum = 100;
        let winWidth = document.documentElement.clientWidth || document.body.clientWidth
        let winHeight = document.documentElement.clientHeight || document.body.clientHeight
        // pc宽屏 大于1326 整体宽度改为百分之80 
        if(winWidth>1326&&this.resultEnvironment==='pc'){
          this.isBigClient = true
          this.currentClientHeight = winHeight + 'px'
          winWidth = winWidth * 0.8
        }else{
          this.isBigClient = false
          this.currentClientHeight = ''
        }
        let html = document.getElementsByTagName("html")[0];
        html.style.fontSize = (winWidth / designWidth) * baseNum + "px";
        // 为了应对接口报错 alert样式错乱的问题
        let timer = setInterval(function() {
          let content = document.getElementsByClassName("el-message__content")[0];
          let messageIcon = document.getElementsByClassName("el-message__icon")[0];
          if (content != undefined) {
            content.style.fontSize = "0.1rem";
            messageIcon.style.fontSize = "0.1rem";
            clearInterval(timer);
           }
        }, 0);
    },
    getStudyReport(data, query) {
      let params = {};
      //如果是wx小程序访问,需要调用不同的后端接口，
      if (query.isWxApp == "1") {
        params = {
          class_lessons_id: data.lesson_id,
          student_id: data.student_id,
        };
        postWeb(data.get_report_func_url, params).then((res) => {
          // 为了取到小程序token 需要修改axois 的baseUrl
          changeBaseUrl(process.env.VUE_APP_SERVER);
          let { error_code, data } = res;
          if (!error_code) {
            this.changeData(data)
          }
        });
      }else {
        params = {
          lesson_id: data.lesson_id,
        };
        this.getStudyReportActions(params).then((res) => {
          let { error_code, data } = res;
          if (!error_code) {
             this.changeData(data)
          }
        });
      }
    },
    changeData(data) {
      this.studyReport = data;
      this.studyReport.preview.task_record_list.forEach((item) => {
        this.getShowValue(item, item.task_group_type);
      });
      this.studyReport.review.task_record_list.forEach((item) => {
        this.getShowValue(item, item.task_group_type);
      });
      // 为了获取 老师课评 评价等级A B C 对应的评语
      if (this.studyReport.assessment.assessment_content != null) {
        this.studyReport.assessment.assessment_content.show_assessment = this.studyReport.assessment.assessment_content.english_assessment;
        if (this.studyReport.assessment.assessment_content.assessment_result.length > 0) {
          this.studyReport.assessment.assessment_content.assessment_result.forEach( (item, index) => {
              if (this.studyReport.assessment.assessment_content .assessment_template_content.length > 0 && this.studyReport.assessment.assessment_content.assessment_template_content[index].options.length > 0) {
                this.studyReport.assessment.assessment_content.assessment_template_content[index].options.forEach((optionItem) => {
                  if (item.result === optionItem.option_key) {
                    item.result_content = optionItem.option_content;
                  }
                });
              }
            }
          );
        }
      }
    },
    getShowValue(item, type) {
      if (type === "1") {
        item.task_group_name = "在线学习建议";
        item.showObj = item.online_task_record;
        item.showObj.star_num = Number(item.showObj.star_num);
      } else if (type === "2") {
        item.task_group_name = "绘本听读";
        item.showObj = item.book_task_record;
        if (item.showObj.total_read_second == 0) {
          item.showObj.time_total = item.showObj.total_read_second;
          item.showObj.time_name = "";
        } else if (
          item.showObj.total_read_second > 0 &&
          item.showObj.total_read_second <= 60
        ) {
          item.showObj.time_name = "s";
          item.showObj.time_total = item.showObj.total_read_second;
        } else if (
          item.showObj.total_read_second > 60 &&
          item.showObj.total_read_second <= 3600
        ) {
          item.showObj.time_total = parseFloat(
            (item.showObj.total_read_second / 60).toFixed(1)
          );
          item.showObj.time_name = "m";
        } else {
          item.showObj.time_total = parseFloat(
            (item.showObj.total_read_second / 3600).toFixed(1)
          );
          item.showObj.time_name = "h";
        }
        item.showObj.star_num = Number(item.showObj.star_num);
      } else if (type === "3") {
        item.task_group_name = "综合学习建议";
        item.showObj = item.offline_task_record;
        item.showObj.star_num = Number(item.showObj.star_num);
      } else {
        item.task_group_name = "学习资源";
      }
    },
    //获取ABC结果背景图
    getResultStyle(index) {
      let imgMapper = {
        0: "red",
        1: "yellow",
        2: "blue",
      };
      let imgPath = require(`@/assets/img/result-bg-${imgMapper[index]}.png`);
      return {
        backgroundImage: `url('${imgPath}')`,
      };
    },
    // 翻译
    translate() {
      this.translateEn = !this.translateEn;
      if (this.translateEn) {
        this.$set(this.studyReport.assessment.assessment_content,"show_assessment",this.studyReport.assessment.assessment_content.english_assessment);
      } else {
        this.$set(this.studyReport.assessment.assessment_content,"show_assessment",this.studyReport.assessment.assessment_content.chinese_assessment);
      }
      this.$forceUpdate();
    },
    back() {
      if (this.resultEnvironment === "pc") {
        this.$router.go(-1);
      } else if (this.resultEnvironment === "mobile") {
        JSbridge.callHandler("closeWebView", null, null);
      } else {
        wx.navigateBack({
          delta: 1,
        });
      }
    },
  },
  mounted() {
    let _this = this
     window.onresize = function(){ // 定义窗口大小变更通知事件
       _this.setRootFontSize()
    };
    // 获得运行环境 phone或pc
    this.resultEnvironment = this.getEnvironment();
    let query = this.$route.query;
    this.isWxApp = query.isWxApp;
    if(query.isWxApp == "1"){
      changeBaseUrl(query.b);
    }
    postWeb("/common/getWebViewInputData", { data_key: query.c }).then(
      (res) => {
        let { error_code, data } = res;
        if (!error_code) {
          this.getStudyReport(data, query);
        }
      }
    );
  },
  computed: {},
  components: {
    star,
  },
  created() {},
  beforeRouteEnter(to, from, next) {
    document.title = '学习报告'
    next((vm) => {
      vm.setRootFontSize();
    });
  },
  destroyed() {
    document.title = '九色鹿学生端'
    let html = document.getElementsByTagName("html")[0];
    html.style.fontSize = "";
  },
  watch: {},
};
</script>

