<template>
    <div class="exam-main-center">
        <!--<div class="wrap-nav">
            <a href="javascript:void(0)" @click="goView('checkEvaluation')">学习测评</a> &gt;
            <a href="javascript:void(0)">测评报告</a> &gt;
            <a href="javascript:void(0)">{{$route.query.test_name}}</a>
        </div>-->
        <iframe-component :iframe-url="iframeUrl"
                          @close="close"
                          style="width: 100%;height: 100%;"
                          iframe-id="unitTestDetailIframe"></iframe-component>
        <!--style="margin:10px 0;height: 93%;width: 100%"-->
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    import iframeComponent from '@/common/iframeComponent.vue'
    let iframeUrl = ''
    export default {
        data: () => ({
            iframeUrl:''
        }),
        beforeRouteEnter(to,from,next){
            let store = window.vm.$store
            let router = window.vm.$router
            let actionMethod = 'checkEvaluation/getLessonExaminationReportWebViewInputDataActions'
            let params = {
                to:to,
                lesson_id:to.query.lesson_id
            }
            store.dispatch(actionMethod,params).then((res)=>{
                let {error_code,data} = res
                if(!error_code){
                    iframeUrl = data.complete_web_view_url
                    if(iframeUrl){
                        next()
                    }
                }else{
                    if(error_code === 20002 || error_code === 20003 || error_code === 20004){
                        router.push({
                            name:'sign_in_success'
                        })
                    }
                }
            })
        },
        methods:{
            close(){
                let vm = window.top.vm
                vm.$router.go(-1)
//                vm.goView('checkEvaluation')
            }
        },
        mounted(){
            if(iframeUrl){
                this.iframeUrl = iframeUrl
            }
        },
        computed: {},
        components: {
            iframeComponent
        },
        created() {
        }
    }
</script>

<style lang="postcss" scoped>

</style>
