<template>
    <div class="exam-main-center">
        <!--scrolling="no"-->
        <iframe-component :iframe-url="iframeUrl"
                          @close="close"
                          @finished="close"
                          style="width: 100%;height: 100%;"
                          iframe-id="taskTestIframe"></iframe-component>
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    import iframeComponent from '@/common/iframeComponent.vue'
    let iframeUrl = ''
    export default {
        data: () => ({
            iframeUrl:''
        }),
        beforeRouteEnter(to,from,next){
            let store = window.vm.$store
            let router = window.vm.$router
            if(to.query.webUrl){
                iframeUrl = to.query.webUrl
                next()
            }else {
                router.push({
                    name:'sign_in_success'
                })
            }
        },
        inject:['routerReload'],
        methods:{
            ...mapActions(['getHomeCardActions']),
            close(){
                let vm = window.top.vm
                vm.$bus.$emit('getHomeCard',true)
                vm.$router.go(-1)
            }
        },
        mounted(){
            if(iframeUrl){
                this.iframeUrl = iframeUrl
            }
        },
        computed: {

        },
        components: {
            iframeComponent
        },
        created() {
        }
    }
</script>

<style lang="postcss" scoped>
</style>
