<template>
  <div class="main-center" v-loading="submitAble">
    <div class="offline-list-box">
      <div class="wrap-nav">
        <a href="javascript:void(0)" @click="goView('courseIndex')">我的课程</a>&gt;
        <a href="javascript:void(0)" @click="goView('courseStudyTypeList', {arrangement_id: $route.query.arrangement_id,task_classify: $route.query.task_classify,})">
          {{$route.query.task_classify === "1" ? "课前资源" : "课后资源"}}</a>&gt;
        <a href="javascript:void(0)" class="current">编程练习建议</a>
      </div>
      <ul>
        <li
          class="common-bor"
          v-for="(item, index) in bianchengList"
          :key="index"
        >
          <div class="left-content">
            <div class="title">
              <span class="dot" v-if="item.is_submit!=='1'"></span>
              编程练习建议 - {{ item.task_name }}
              <span class="correct-tip" v-if="item.is_submit==='0'&&item.is_pigeonhole==='1'">订</span>
              <!--<span class="tip">{{item.submit_type==='1'?'线上':'线下'}}</span>-->
            </div>
            <div class="lesson-name">{{ item.arrangement_name }}</div>
            <div class="course-name">{{ item.course_name }}</div>
          </div>
          <div class="right-content">
            <div class="time">发布时间：{{ item.create_date }}</div>
            <!--'disabled':item.is_submit==='1' && item.check_status === '0'-->
            <button class="btn" @click="goOfflineTestDetail(item)"
                    :class="{'btn-white':item.is_submit==='1','btn-orange':item.is_submit!=='1'}">{{item.is_submit==='1'?'查看详情':item.is_pigeonhole==='1'?'去订正':'去完成'}}</button>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data: () => ({
    bianchengList: [],
  }),
  methods: {
    ...mapActions("course", ["getTaskCodeListActions"]),
    getWritingTaskList() {
      let params = {
        course_arrangement_id: this.$route.query.arrangement_id,
        task_classify: this.$route.query.task_classify,
      };
      this.getTaskCodeListActions(params).then((res) => {
        let { error_code, data } = res;
        if (!error_code) {
          this.bianchengList = data;
        }
      });
    },
    goOfflineTestDetail(item) {
//      if(item.is_submit==='1' && item.check_status === '0'){
//        this.$message.warning('作品已提交，请耐心等待老师批改哦~')
//        return
//      }
       let self = this
       let params = {
        task_id:item.student_task_id,
        task_classify: item.task_classify,
        fromRouterName: 'onlineBianchengList',
        arrangement_id:self.$route.query.arrangement_id,
        subjectType:'12'
       }
       this.goView('bianchengDetail',params)
    },
  },
  watch: {},
  mounted() {
    this.getWritingTaskList();
  },
  computed: {},
  components: {},
  created() {},
};
</script>

<style lang="postcss" scoped>
.main-center {
  & .offline-list-box {
    padding: 2.5rem 2.5rem;
    & ul {
      & li {
        height: 8.75rem;
        margin-top: 1.25rem;
        position: relative;
        & .left-content {
          position: absolute;
          left: 1.25rem;
          top: 1.33rem;
          & .title {
            height: 1.58rem;
            font-size: 1.33rem;
            font-family: MicrosoftYaHei;
            color: #000000;
            line-height: 1.58rem;
            & .dot {
              width: 0.58rem;
              height: 0.58rem;
              background: #E02020;
              display: inline-block;
              border-radius: 1rem;
              vertical-align: middle;
              margin-right:0.22rem;
            }
            & .correct-tip {
              display: inline-block;
              width: 1.5rem;
              height: 1.5rem;
              background: #FF1D22;
              border-radius: 0.42rem;
              font-size: 0.92rem;
              font-family: MicrosoftYaHei;
              color: #FFFFFF;
              text-align: center;
              margin-left:0.43rem;
            }
            & .tip {
              display: inline-block;
              width: 2.67rem;
              height: 1.5rem;
              background: #915EE6;
              border-radius: 0.42rem;
              font-size: 0.92rem;
              font-family: MicrosoftYaHei;
              color: #FFFFFF;
              margin-left:0.83rem;
              text-align: center;
            }
          }
          & .lesson-name{
            margin-top:1.67rem;
            height: 1.33rem;
            font-size: 1.17rem;
            font-family: ArialMT;
            color: #666666;
            line-height: 1.33rem;
          }
          & .course-name {
            margin-top:0.42rem;
            height: 1.17rem;
            font-size: 1rem;
            font-family: MicrosoftYaHei;
            color: #999999;
            line-height: 1.17rem;
          }
        }
        & .right-content {
          position: absolute;
          right: 1.25rem;
          top: 1.5rem;
          display: flex;
          flex-direction: column;
          align-items: flex-end;
          & .time {
            height: 1.17rem;
            font-size: 1rem;
            font-family: MicrosoftYaHei;
            color: #999999;
            line-height: 1.17rem;
          }
          & button {
            margin-top: 2.33rem;
          }
        }
      }
    }
  }
}
</style>
