<template>
    <div class="exam-main-center">
        <!--<div class="wrap-nav">
            <a href="javascript:void(0)" @click="goView('courseTask')">课程任务</a> &gt;
            <a href="javascript:void(0)">{{navName}}</a> &gt;
            <a href="javascript:void(0)">{{$route.query.lesson_name}}</a>
        </div>-->
        <iframe-component :iframe-url="iframeUrl"
                          @close="close"
                          style="width: 100%;height: 100%;"
                          iframe-id="testDetailIframe"></iframe-component>
        <!--style="margin:10px 0;height: 93%;width: 100%"-->
    </div>
</template>

<script>
    import {mapActions} from 'vuex'
    import iframeComponent from '@/common/iframeComponent.vue'
    let iframeUrl = ''
    export default {
        data: () => ({
            iframeUrl:''
        }),
        beforeRouteEnter(to,from,next){
            // let store = window.vm.$store
            // let router = window.vm.$router
            // let actionMethod = 'course/getTaskReportWebViewInputDataActions'
            // let params = {
            //     to:to,
            //     task_id:to.query.task_id,
            // }
            // store.dispatch(actionMethod,params).then((res)=>{
            //     let {error_code,data} = res
            //     if(!error_code){
            //         iframeUrl = data.complete_web_view_url
            //         if(iframeUrl){
            //             next()
            //         }
            //     }else{
            //         if(error_code === 20002 || error_code === 20003 || error_code === 20004){
            //             router.push({
            //                 name:'sign_in_success'
            //             })
            //         }
            //     }
            // })
            let store = window.vm.$store
            let router = window.vm.$router
            if(to.query.webUrl){
                iframeUrl = to.query.webUrl
                next()
            }else {
                router.push({
                    name:'sign_in_success'
                })
            }
        },
        methods:{
            ...mapActions('course',['getTaskReportWebViewInputDataActions']),
            close(){
                let vm = window.top.vm
                vm.$router.go(-1)
//                vm.goView('courseTask')
            }
        },
        mounted(){
            if(iframeUrl){
                this.iframeUrl = iframeUrl
            }
        },
        computed: {
            navName(){
                let result = ''
                let task_type = this.$route.query.task_type
                result = task_type?(task_type==='1'?'课前练习':'课后练习'):'-'
                return result
            }
        },
        components: {
            iframeComponent
        },
        created() {
        }
    }
</script>

