import { mapActions } from 'vuex';
import iframeComponent from '@/common/iframeComponent.vue';
var iframeUrl = '';
var fromRouterName = '';
var fromRouterQuery = '';
export default {
  data: function data() {
    return {
      iframeUrl: ''
    };
  },
  beforeRouteEnter: function beforeRouteEnter(to, from, next) {
    var store = window.vm.$store;
    var router = window.vm.$router;

    if (to.query.webUrl) {
      iframeUrl = to.query.webUrl;
      fromRouterName = to.query.formRouterName;
      fromRouterQuery = JSON.parse(to.query.formRouterQuery);
      next();
    } else {
      router.push({
        name: 'sign_in_success'
      });
    }
  },
  methods: {
    close: function close() {
      var vm = window.top.vm; //                vm.$router.go(-1)

      vm.$router.push({
        name: fromRouterName,
        query: fromRouterQuery
      });
    },
    closeBySubmit: function closeBySubmit() {
      vm.$router.push({
        name: 'worksManage'
      });
    }
  },
  mounted: function mounted() {
    if (iframeUrl) {
      this.iframeUrl = iframeUrl;
    }
  },
  computed: {},
  components: {
    iframeComponent: iframeComponent
  },
  created: function created() {}
};